@font-face {
    font-family: Vag Rounded;
    src: url(//db.onlinewebfonts.com/c/50caebd3d1f303be2ec212f78f8c084e?family=VAG+Rounded+Std);
}

@font-face {
    font-family: "Myriad Pro";
    src: url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.eot");
    src: url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.eot?#iefix") format("embedded-opentype"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.woff2") format("woff2"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.woff") format("woff"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.ttf") format("truetype"),
    url("//db.onlinewebfonts.com/t/3dd6e9888191722420f62dd54664bc94.svg#Myriad Pro") format("svg");
}

@font-face {
    font-family: "Myriad Pro Semibold";
    src: url("Myriad Pro Semibold.ttf") format("truetype");
}

@font-face {
    font-family: "Myriad Pro Regular";
    src: url("MYRIADPRO-REGULAR.woff") format("woff");
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.landing-container {
    width: 100%;
    height: 200rem;
    font-family: Roboto;
    font-size: small;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 0 5rem 0;
}

.landing-intro {
    font-family: "Myriad Pro Semibold";
}

.landing-intro p {
    margin-top: 20px;
}

.landing-intro h1 {
    background-color: #010f3e;
    padding: 10px;
    color: white;
    font-size: medium;
    font-family: "Myriad Pro Semibold";

}

.steps h2 {
    background-color: white;
    padding: 10px;
    color: #00b0ed;
    font-size: medium;
}

.landing-subtitle {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    width: 90%;
    margin-top: 1rem;
    font-size: x-small;
    font-family: "Myriad Pro Semibold";
    color: #010f3e;
}

.landing-subtitle h1 {
    font-family: "Myriad Pro Semibold";
}


.steps h2 {
    font-size: small;
    margin-left: 2rem;
    font-family: "Myriad Pro Semibold";

}

.landing-container .subtitle {
    display: flex;
    justify-content: flex-start;
    margin-left: 220px;
}

.landing-container .subtitle h1 {
    margin-left: 5px;
    padding-bottom: -10px;
    margin-bottom: -10px;
}

.steps ul {
    list-style-type: circle;
    margin-left: 4rem;
    color: #010f3e;

}

.home-section {
    display: flex;
    flex-direction: column;
}

@media (min-width: 992px) {
    .landing-container {
        width: 100vw;
        height: 10vw;
        font-size: medium;
        padding: 0 10rem 10rem 10rem;
    }

    .landing-intro {
        display: flex;
        justify-content: center;
        flex-direction: column;
        width: 90%;
        margin-top: 2%;
    }

    .landing-intro p {
        margin-top: 20px;
    }

    .landing-intro h1 {
        background-color: #010f3e;
        color: white;
        font-size: large;
    }

    .steps {
        margin-left: 20px;
        font-family: "Myriad Pro Regular";
    }

    .landing-subtitle {
        margin-top: 3%;
        font-size: x-large;
    }

    .landing-container .subtitle {
        display: flex;
        justify-content: flex-start;
        margin-left: 220px;
    }

    .landing-container .subtitle h1 {
        margin-left: 5px;
        padding-bottom: -10px;
        margin-bottom: -10px;
        font-size: x-large;
    }

    .steps h2 {
        font-size: medium;
        font-family: "Myriad Pro Semibold";
    }
}

.steps h2 {
    font-size: medium;
    font-family: "Myriad Pro Semibold";
}

.steps {
    margin-left: 20px;
    font-family: "Myriad Pro Regular";
}
