  .arrow {
    width: 3vw;
    height: 3vw;
    border: 2.5vw solid;
    border-color: #010f3e transparent transparent #010f3e;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
  }

  .arrowSliding {
    position: absolute;
    -webkit-animation: slide 4s linear infinite;
    animation: slide 4s linear infinite;
  }

  .delay1 {
    -webkit-animation-delay: 3s;
    animation-delay: 3s;
  }

  .delay2 {
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
  }

  .delay3 {
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
  }

  @-webkit-keyframes slide {
    0% { opacity:0; -webkit-transform: translateX(-15vw); transform: translateX(-15vw);
    }

    20% { opacity:1; -webkit-transform: translateX(-9vw); transform: translateX(-9vw);
    }

    80% { opacity:1; -webkit-transform: translateX(9vw); transform: translateX(9vw);
    }

    100% { opacity:0; -webkit-transform: translateX(15vw); transform: translateX(15vw);
    }
  }

  @keyframes slide {
    0% { opacity:0; -webkit-transform: translateX(-15vw); transform: translateX(-15vw);
    }

    20% { opacity:1; -webkit-transform: translateX(-9vw); transform: translateX(-9vw);
    }

    80% { opacity:1; -webkit-transform: translateX(9vw); transform: translateX(9vw);
    }

    100% { opacity:0; -webkit-transform: translateX(15vw); transform: translateX(15vw);
    }
  }

  .banner_text{
    display: block;
    font-size: 4rem;
  }
