footer {
    color: white;
    font-size: small;
    background-color: #010f3e;
    padding: 25px 0;
    margin-top: 100px;
    font-family: Roboto;
}

.footerContainerOne {
    display: flex;
    align-items: stretch;
    justify-content: center;
    flex-direction: column;
    margin-top: 3rem;
}

.insideFooterContainerOne {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    margin-bottom: 2rem;
    margin-left: -1rem;
    justify-content: flex-start;
}

.regulatory-footer {
    margin-bottom: -2rem;
}

.social-link {
    color: #00b0ed;
    font-size: large;
    margin-right: 6px;
    margin-top: 100px;

}

.footer-nav-links {
    display: flex;
    flex-direction: row;
    flex-flow: row;
    align-content: space-between;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.footer-nav-link {
    display: list-item;
    list-style-type: circle;
    list-style-position: inside;
}

.footer-navex-logo div {
    margin: 20px;
    justify-content: space-between;
}

.footer-navex-logo button {
    background-color: #00b0ed;
}

.insideFooterContainerTwo {
    margin-top: -2rem;
}

.insideFooterContainerThree {
    margin-left: 0;
    font-family: VAG Rounded Std;
}

.insideFooterContainerThree h3{
    margin-bottom: -1rem;
    color: #00b0ed;
}

.email-logo {
    color: #00b0ed;
    font-size: large;
    margin: 10px;
}

.email-logo span {
    color: white;
    font-size: small;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: normal;
    margin-left: 10px;
    margin-bottom: 10px;
}

.report-button {
    width: 100%;
    margin: 2rem 1rem 1rem;
}

.regulatory-footer img {
    width: 100%;
}

.borrow-caution h3 {
    font-family: VAG Rounded Std;
}

.borrow-caution {
    margin-top: -4rem;
    align-content: space-between;
}

.borrow-caution h3{
    color: #00b0ed;
    margin-bottom: -1rem;
    width: 70%;
}

.footer-buttons{
    font-family: "VAGRounded";
}

.navex-logo {
    height: 10rem;
    width: 10rem;
    margin-left: 2rem;
    margin-right: 4rem;
    margin-top: 5rem;
}

@media (min-width: 992px) {
    footer {
        padding-left: 5rem;
    }

    section {
        margin: 25px 0 25px 100px;
        width: 60%;
        /* border: red 2px solid; */
    }

    section .squre {
        background-color: #00b0ed;
        margin: 0 0 5px 0;
        width: 25px;
        height: 25px;
    }

    section h5 {
        margin: 0 0 10px 0;
        color: #010f3e;
    }

    section h1 {
        margin: 0 0 10px 0;
        color: #010f3e;
    }

    section p {
        margin: 0 0 10px 0;
        font-size: small;
    }

    .regulatory-footer {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 1rem;
    }

    .borrow-caution {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        text-align: start;
        margin-top: 0;

    }

    .borrow-caution h3 {
        font-size: large;
    }

    .borrow-caution span {
        width: 25rem;
        font-size: medium;
        font-family: Myriad Pro;
    }

    footer {
        color: white;
        font-size: small;
        background-color: #010f3e;
        padding: 25px 100px 25px 100px;
        margin-top: 300px;
        font-family: Roboto;
    }

    footer .footerContainerOne {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        margin-top: -4rem;
    }

    footer .footerContainerTwo p {
        margin: 25px 0 0 0;
    }

    footer .footerContainerTwo p a {
        margin: 25px 0 0 0;
        color: #00b0ed;
    }

    .footer-logo {
        margin-left: -1rem;
        margin-right: 1.8rem;
    }

    .fab {
        color: #00b0ed;
    }

    .social-link {
        color: #00b0ed;
        font-size: x-large;

    }

    .insideFooterContainerTwo {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 0;
    }

    .insideFooterContainerThree {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        text-align: start;
        margin-top: 0;
    }

    .insideFooterContainerThree h3 {
        font-size: large;
    }

    .insideFooterContainerThree span {
        width: 25rem;
        font-size: medium;
        font-family: Myriad Pro;
    }

    .footer-links {
        display: flex;
        flex-direction: column;
        flex-flow: column;
        list-style-type: disc;
        list-style-position: inside;
        align-self: start;
        font-size: medium;
        margin: 0px;
    }

    .insideFooterContainerOne {
        display: flex;
        flex-direction: column-reverse;

    }

    .fas {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-bottom: 10px;
        justify-content: space-between;
    }

    .footer-links:hover {
        cursor: pointer;
    }

    .footer-navex-logo {
        display: inherit;
        flex-direction: column;
    }

    .footerContainerTwo {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-top: -2rem;
    }

    .email-logo {
        margin: 0;
    }

    .email-logo span {
        margin-left: -15rem;
        margin-bottom: 0;
    }

    .insideFooterContainerFour {
        display: flex;
        flex-flow: column;
        width: 13rem;
        margin-right: 4.5rem;
    }

    .phone-number{
        display: flex;
        flex-flow: row;
        justify-content: space-evenly;
    }

    .op-hours pr{
        font-size: medium;
        margin-left: 0.4rem;

    }

    .report-button {
        width: 19.7%;

    }

    .footer-nav-links {
        display: flex;
        flex-flow: column;
        margin-bottom: 1rem;
    }

    .footer-nav-link {
        display: list-item;
        list-style-type: circle;
        list-style-position: inside;
    }

    .email-icon {
        display: inherit;
        flex-flow: row;
        justify-content: space-evenly;
        font-size: 14px;

    }

    .footer-links {
        justify-content: start;
    }
}

